import type { AppProps } from 'next/app';
import { useEffect } from 'react';
import { AuthProvider } from '@/contexts/AuthContext';
import { WebSocketProvider } from '@/contexts/WebSocketContext';
import Navbar from '@/components/layout/Navbar';
import AppBootstrapGuard from '@/components/AppBootstrapGuard';
import '@/styles/globals.css';

function MyApp({ Component, pageProps }: AppProps) {
  useEffect(() => {
    // Apply dark mode by default
    document.documentElement.classList.add('dark');

    // AUTH EXPIRY LISTENER
    const handleAuthExpired = () => {
      console.warn('üîê Auth expired event received - redirecting to /auth')
      // Clear any stored auth data
      localStorage.removeItem("upstox_auth")
      sessionStorage.removeItem("upstox_auth")

      // Redirect to auth screen
      window.location.href = "/auth"
    }

    // Add global event listener for auth expiry
    window.addEventListener("auth-expired", handleAuthExpired)

    // üîç GLOBAL REST CALL INTERCEPTOR FOR AUDIT
    const originalFetch = window.fetch;
    window.fetch = async (...args) => {
      console.log("üåê REST CALL DETECTED:", args[0]);
      console.log("üåê REST METHOD:", args[1]?.method || 'GET');
      console.log("üåê REST TIMESTAMP:", new Date().toISOString());

      const start = performance.now();
      const response = await originalFetch(...args);
      const duration = performance.now() - start;

      console.log("üåê REST STATUS:", response.status);
      console.log("üåê REST DURATION:", `${duration.toFixed(2)}ms`);

      return response;
    };

    // üîç AXIOS INTERCEPTOR (if axios is used)
    if (typeof window !== 'undefined' && (window as any).axios) {
      (window as any).axios.interceptors.request.use((config: any) => {
        console.log("üåê AXIOS REST CALL:", config.url);
        console.log("üåê AXIOS METHOD:", config.method);
        return config;
      });
    }

    console.log("üîç REST/WINDOW INTERCEPTORS INSTALLED");

    // Cleanup on unmount
    return () => {
      window.removeEventListener("auth-expired", handleAuthExpired)
    }
  }, []);

  return (
    <AuthProvider>
      <AppBootstrapGuard>
        <div className="min-h-screen bg-background text-text-primary">
          <Navbar />
          <Component {...pageProps} />
        </div>
      </AppBootstrapGuard>
    </AuthProvider>
  );
}

export default MyApp;
